import React, { useState } from "react";
import "./App.css";
import Typing from "./Typing";
import Dashboard from "./Dashboard/Dashboard";
import Stats from "./Dashboard/Stats";
import Wordbanks from "./Dashboard/Wordbanks";
import DataAndSettings from "./Dashboard/DataAndSettings";
import Graph from "./Dashboard/Graph";
import useWordbankActive from "../hooks/useWordbankActive";
import useCangjieDicts from "../hooks/useCangjieDicts";
import useWordbanks from "../hooks/useWordbanks";
import usePractice from "../hooks/usePractice";
import useTypingHistory from "../hooks/useTypingHistory";
import useGraph from "../hooks/useGraph";
import useIme from "../hooks/useIme";
import Lookup from "./Lookup";
import { toKey } from "../util/toInternalKey";
import useScreenKeyboard from "../hooks/useScreenKeyboard";

function App() {
  return <AppWithContext />;
}

function AppWithContext() {
  const sections = useWordbanks();
  const { wordbankActive, setWordbankActive } = useWordbankActive();
  const cangjieDicts = useCangjieDicts();

  const [lookupCharacter, setLookupCharacter] = useState("");

  const {
    wordQueue,
    currentWordProgress,
    getCompositionTarget,
    onComposition: practiceOnComposition
  } = usePractice({ sections, wordbankActive, setLookupCharacter });
  const {
    historyRef,
    setHistory,
    onKey: thOnKey,
    onComposition: thOnComposition
  } = useTypingHistory();
  const {
    onKey: graphOnKey,
    onComposition: graphOnComposition,
    getRecentStats: graphGetRecentStats
  } = useGraph();

  const { ime, enterKey: imeEnterKey } = useIme({
    dicts: cangjieDicts,
    getCompositionTarget,
    onComposition: [practiceOnComposition, thOnComposition, graphOnComposition]
  });

  const { keyboard, setKey: setScreenKeyboardKey } = useScreenKeyboard(imeEnterKey);

  return (
    <div className="App">
      <Lookup dicts={cangjieDicts} character={lookupCharacter} setCharacter={setLookupCharacter} />
      <Typing
        wordQueue={wordQueue}
        currentWordProgress={currentWordProgress}
        composerKeys={ime.composerKeys}
        handleKeyDown={(e) => {
          const k = toKey(e.code);
          if (k === null) return;
          thOnKey(k);
          graphOnKey(k);
          imeEnterKey(k);
          setScreenKeyboardKey(true, k);
        }}
        handleKeyUp={(e) => {
          const k = toKey(e.code);
          if (k === null) return;
          setScreenKeyboardKey(false, k);
        }}
        setLookupCharacter={setLookupCharacter}
      />
      <Dashboard
        wordbanks={<Wordbanks sections={sections} wordbankActive={wordbankActive} setWordbankActive={setWordbankActive} />}
        graph={<Graph getRecentStats={graphGetRecentStats} />}
        keyboard={keyboard}
        stats={<Stats history={historyRef.current} />}
        dateAndSettings={<DataAndSettings historyRef={historyRef} setHistory={setHistory} />}
      />
      <div className="Footer">
        <a href="https://github.com/joshvictor1024/type-cangjie">
          <svg className="Footer__github" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
            <path d="M12 .297c-6.63 0-12 5.373-12 12 0 5.303 3.438 9.8 8.205 11.385.6.113.82-.258.82-.577 0-.285-.01-1.04-.015-2.04-3.338.724-4.042-1.61-4.042-1.61C4.422 18.07 3.633 17.7 3.633 17.7c-1.087-.744.084-.729.084-.729 1.205.084 1.838 1.236 1.838 1.236 1.07 1.835 2.809 1.305 3.495.998.108-.776.417-1.305.76-1.605-2.665-.3-5.466-1.332-5.466-5.93 0-1.31.465-2.38 1.235-3.22-.135-.303-.54-1.523.105-3.176 0 0 1.005-.322 3.3 1.23.96-.267 1.98-.399 3-.405 1.02.006 2.04.138 3 .405 2.28-1.552 3.285-1.23 3.285-1.23.645 1.653.24 2.873.12 3.176.765.84 1.23 1.91 1.23 3.22 0 4.61-2.805 5.625-5.475 5.92.42.36.81 1.096.81 2.22 0 1.606-.015 2.896-.015 3.286 0 .315.21.69.825.57C20.565 22.092 24 17.592 24 12.297c0-6.627-5.373-12-12-12"></path>
          </svg>
        </a>
      </div>
    </div>
  );
}

export default App;
